/*
 uni.getStorage  uni.setStorage  Promise 

	

uni.getStorageuni.setStorage  successfail 
*/ 

export const getStorage = function(key) {
	return new Promise((resolve, reject) => {
		
		uni.getStorage({
			key:key,
			success(res) {
				resolve(res.data)
			},
			fail() {
				resolve(null)
			}
		})
		
	})
}

export const setStorage = function(key, data) {
	return new Promise((resolve, reject) => {
		
		uni.setStorage({
			key:key,
			data:data,
			success() {
				resolve(true)
			},
			fail() {
				resolve(false)
			}
		})
		
	})
}

export const cachedFileKey = 'cachedFileKey'